import Vue from 'vue'
import VueRouter from 'vue-router'
import {routerUtils} from '@/utils/RouterUtils.js'
import Search from '@/views/search' // 搜索主页
import Display from '@/views/display' // 播放页
import SearchList from '@/views/search-list'// 搜索列表展示页
import DisplayNew from '@/views/display/DisplayNew'

Vue.use(VueRouter)

// 2. 定义路由
// 每个路由应该映射一个组件。 其中"component" 可以是
// 通过 Vue.extend() 创建的组件构造器，
// 或者，只是一个组件配置对象。
// 我们晚点再讨论嵌套路由。
const routes = [
    {path: '/', component: Search},
    {
        path: '/searchList', component: SearchList,
        props: route => ({id: route.query.wd})
    },
    {
        path: '/display', component: Display,
        props: route => ({id: route.query.wd})
    },
    {
        path: '/displayNew', component: DisplayNew,
        props: route => ({id: route.query.wd})
    },]

// 3. 创建 router 实例，然后传 `routes` 配置
// 你还可以传别的配置参数, 不过先这么简单着吧。
const router = new VueRouter({
    routes // (缩写) 相当于 routes: routes
})


routerUtils.registerRouter(router)
export {router}

