<template>
  <div>
    <el-page-header @back="goBack" content="播放" class="page-header">
    </el-page-header>
    <IframeVideo :url="iframeUrl" ref="IframeVideo"></IframeVideo>
    <div style="box-sizing: border-box; width: 100%; padding: 0px 5px">
      <el-select
          placeholder="请选择"
          style="width: 80%"
          v-model="parseUrl"
          @change="reload"
      >
        <el-option
            v-for="item in videoParseUrls"
            :key="item.id"
            :label="item.name"
            :value="item.url"
        >
        </el-option>
      </el-select>
      <el-button
          icon="el-icon-refresh"
          circle
          style="margin-left: 20px"
          @click="reload"
      ></el-button>
    </div>
    <hr/>
  </div>
</template>

<script scoped>
import IframeVideo from '@/components/IframeVideo'
import {routerUtils} from '@/utils/RouterUtils.js'
import {displayList, parseUrls} from "../../api/display";

export default {
  name: 'DisplayNew',
  components: {
    IframeVideo,
  },
  props: {
    id: String,
  },
  data: function () {
    return {
      active: 0,
      parseUrl: 'https://jx.xmflv.com/?url=',
      videoParseUrls: [],
      url: '',
    }
  },
  methods: {
    goBack() {
      routerUtils.push({path: '/'})
    },

    reload() {
      this.$refs.IframeVideo.reloadIframe()
    },
  },

  computed: {
    iframeUrl() {
      let displayUrl = this.parseUrl + this.url
      console.log('displayUrl==>' + displayUrl)
      return displayUrl
    },
  },

  watch: {
    id() {
      console.log('watch.id==>' + this.id)
      this.url = this.id
    },
  },

  beforeCreate() {
    // 加载视频播放解析地址
    parseUrls().then(res => {
      let data = res.data
      this.parseUrl = data.defalutParseUrl
      this.videoParseUrls = data.videoParseUrls
    })
  },

  created() {
    this.url = this.id
  },

  beforeMount() {
  },

  mounted() {
  },
}
</script>

<style lang="css" scoped>
.page-header {
  height: 40px;
  line-height: 40px;
  background-color: rgb(239, 241, 242);
}

.active {
  background-color: #909399 !important;
  border-color: #909399 !important;
  color: #fff !important;
}
</style>
