
class RouterUtils {
    router;
    constructor(router = null) {
        this.router = router
    }

    registerRouter(router = null) {
        this.router = router
    }

    getRouter() {
        if (this.router == null) {
            throw new Error("没有注册router，请先注册")
        }
        return this.router
    }

    go(num = 1) {
        this.getRouter().go(num)
        return this
    }

    back(num = 1) {
        this.getRouter().go(-num)
        return this
    }
    push(obj = null) {
        if (obj == null) {
            return this
        }
        this.getRouter().push(obj)
        return this
    }
}

const routerUtils = new RouterUtils()

export { routerUtils }