<template>
  <div>
    <iframe
      :style="iframeStyle"
      v-if="isShowIframe"
      allowfullscreen
      allowtransparency
      frameborder="0"
      :src="url"
      ref="iframeId"
    >
    </iframe>
  </div>
</template>

<script scoped>
export default {
  name: 'IframeVideo',
  data: function () {
    return {
      iframeStyle: 'width: 100%',
      isShowIframe: true,
    }
  },
  props: {
    url: String,
    callback: Function,
  },

  methods: {
    reloadIframe: function () {
      this.isShowIframe = !this.isShowIframe
      window.setTimeout(() => {
        this.isShowIframe = !this.isShowIframe
      }, 0)
    },

    // 适配视频播放尺寸
    adapterIframe: function () {
      let width = this.$refs.iframeId.offsetWidth
      let height = width * (9 / 16)
      this.iframeStyle = `width:${width}px;height:${height}px`
    },
  },

  created() {},

  mounted() {
    this.adapterIframe()
    this.adapterIframe()
  },
}
</script>

<style scoped></style>
